import React from 'react'

const Card = (props) => {
  return (
    <div className='card'>
        <div className='icon'><i className={props.icon}></i></div>
        <div className='service'><h3>{props.service}</h3></div>
        <div className='details'><small>{props.details}</small></div>
    </div>
  )
}

export default Card