import React from 'react'

const Deal = (props) => {
    return (
        <a href={props.link} className='deal'>
            <div className='img-box'>
                <img src={props.image} alt={props.altimage} />
            </div>
            <div className='main'>
                <div className='seller'><small>: <br/></small>{props.sallername} </div>
                <div className='address'>{props.name} <br/><small><i class="fa fa-map-marker" aria-hidden="true"></i> {props.address} </small></div>
            </div>
            <div className='about'>
                <span className='space'><small>: </small>{props.space} </span>
                <span className='size'><small>: </small>{props.size} </span>
            </div>
            <div className='price'>{props.price}.</div>
        </a>
    )
}

export default Deal