import React from 'react'
import Sponsor from './Sponsor'
import Sponsor1 from './images/sponsor1.webp'
import Sponsor2 from './images/download.webp'
import Sponsor3 from './images/download1.webp'
import Sponsor4 from './images/download2.webp'
import Sponsor5 from './images/download3.webp'

const Sponsors = () => {
  return (
    <section className='sponsors'>
        <div className='container'>
        <Sponsor image={Sponsor1} />
        <Sponsor image={Sponsor2} />
        <Sponsor image={Sponsor3} />
        <Sponsor image={Sponsor4} />
        <Sponsor image={Sponsor5} />
        </div>
    </section>
  )
}

export default Sponsors